<template>
	<view class="time">
		<!-- 距结束还有 -->
		{{ h1text }}
		<view class="time-box">{{ timeArr[0] }}</view>
		:
		<view class="time-box">{{ timeArr[1] }}</view>
		:
		<view class="time-box-red">{{ timeArr[2] }}</view>
		<!-- <view class="time-box-red">{{ timeArr[3] }}</view> -->
	</view>
</template>

<script>
export default {
	props: {
		h1text: {
			type: String
		},
		timeArr: {
			type: Array
		}
	},
};
</script>

