.time {
	font-size: 26rpx;
	color: #ffffff;
	display: flex;
	height: 36rpx;

	.time-box {
		background-color: #545454;
		padding: 0 0.2em;
		margin: 0 0.5em;
		border-radius: 8rpx;
	}

	.time-box-red {
		background-color: #ea3a37;
		padding: 0 0.2em;
		margin: 0 0.5em;
		border-radius: 8rpx;
		width: 2em;
		text-align: center;
	}
